@font-face {
    font-family: 'LGC';
    src: url('../../../font/Louis\ George\ Cafe.ttf'), format('ttf');


    font-family: 'LGC-B';
    src: url('../../../font/Louis\ George\ Cafe.ttf'), format('ttf');

    font-family: 'MontBold';
    src: url('../../../font/Mont-HeavyDEMO.otf'), format('otf');
}
.panel-container{
    width: 100vw;
    height: 800px;
    background-color: black;
}

.panel {
    background-color: white;
    width: 80%;
    height: 80%;
    
    justify-content: center;
    transform: translateY(8rem);
    border-radius: 6px;

    padding-bottom: 240px;

    display:flex;
    flex-direction: column;
    box-shadow: 0 0 2em 0 #0d26e7;
    position: relative;
}

.panel h1 {
    font-family: 'MontBold';
    align-self: center;
    height: auto;
    font-size: 3rem;
    font-weight: lighter;
    text-decoration: underline;
    text-underline-offset: 0.5em;
    transform: translateY(1rem);
}

.panel p {
    font-family: 'LGC';
    height: auto;
    margin-left: 4rem;
    margin-right: 4rem;
    align-self: center;
    text-align: center;
    font-size: 24px;
}

.panel button {
    font-family: 'LGC';
    height: auto;
    margin-left: 4rem;
    margin-right: 4rem;
    align-self: center;
    text-align: center;
}

.parallex-effect {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 80%;
}

.panel-username-input{
    align-self: center;
    height:17px;
    border: 0;
    width: 40%;
    margin-left:1px;
    margin-bottom: 4em;
    box-shadow: -8px 10px 0px -7px #0d26e7, 8px 10px 0px -7px #0d26e7;
    -webkit-transition: box-shadow 0.3s;
    transition: box-shadow 0.3s;
    text-align: center;
    margin-top: 10rem;
}

.panel-username-input:focus {
    outline: none;
    box-shadow: -8px 10px 0px -7px #000000, 8px 10px 0px -7px #000000;
    
}

.special-text-purple{
    color: #0d26e7;
}

.special-text-lime{
    color: #0d26e7;
}

.sound-box-container{
    display: flex;
    justify-content:space-evenly;
    flex-direction: row;
    width: auto;
    height: 100%;
    margin-left: 1rem;
    margin-right: 1rem;
}

@media only screen and (max-width: 320px){
    .parallex-effect{
        height: 100%;
    }
    .sound-box-container{
        padding: 0;
        transform: translateY(5px);
    }
    .panel{
        height: 100%;
        width: 100%;
        margin-top: 5rem;
        padding-bottom: 55px;
    }
    .panel h1{
        font-size: 7px !important;
        margin-top: 40px !important;
        transform: translateY(8px);
    }
    .panel p{
        font-size: 6px !important;
        margin-left: 20px;
        margin-right: 20px;
        
    }
    .panel-username-input{
        margin: 0;
    }
    .panel-container{
        height: 100%;
    }
}

