.sound-box{
    width:30%;
    margin-left: 10px;
    margin-right: 10px;
    background-color: #000000;
    color: white;
    border-radius: 10px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    vertical-align: middle;
    padding-bottom: 20px;
}

.sound-box svg{
    width: 100px;
    height: 100px;
    margin-top: 30px;
    background: linear-gradient(90deg, rgb(78,140,255), rgb(170, 170, 252));
    transition: transform, color 0.2s;
    padding: 20px;
    border-radius: 10px;
}

.sound-box svg:hover{
    color: #f9f9f9;
    cursor: pointer;
    transform: scale(1.05);
}

.sound-box p{
    font-size: 20px;
    align-self: center;
}

.sound-box input{
    align-self: center;
    height: 8px;
    width: 80%;
}


input[type="range"]::-webkit-slider-thumb {
    width: 25px; /* adjust to your desired width */
    height: 25px; /* adjust to your desired height */
    background-color: #000000; /* set the background color */
    border-radius: 50%; /* make the thumb circular */
    -webkit-appearance: none; /* remove the default appearance */
    cursor: pointer; /* add a cursor */
  }
  
  /* for Firefox */
  input[type="range"]::-moz-range-thumb {
    width: 25px; /* adjust to your desired width */
    height: 25px; /* adjust to your desired height */
    background-color: #000000; /* set the background color */
    border-radius: 50%; /* make the thumb circular */
    cursor: pointer; /* add a cursor */
  }


@media only screen and (max-width: 320px){  
    .sound-box-container{
        display:flex;
        flex-direction: column;
        justify-content: space-evenly;
    }
    .sound-box{
        margin: 0;
