@font-face {
    font-family: 'LGC';
    src: url('../../../font/Louis\ George\ Cafe.ttf'), format('ttf');


    font-family: 'LGC-B';
    src: url('../../../font/Louis\ George\ Cafe.ttf'), format('ttf');

    font-family: 'MontBold';
    src: url('../../../font/Mont-HeavyDEMO.otf'), format('otf');
}

.modal-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 9999;
  }
  
  .modal-box {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
    width: 1000px;
    height: 1000px;
    z-index: 999;

    display: flex;
    justify-content: center;
    flex-direction: column;
    overflow: hidden;
  }

  .modal-header {
    background-color: black;
    width: 100%;
    height: 8%;
  }

    .modal-header p{
        color: white;
        font-family: 'MontBold';
        padding-left: 10px;
    }
    
    .modal-header button{
        color: white;
        font-family: 'MontBold';
    }

  .modal-content{
    height: 92%;
    width: 100%;
    display: flex;
    justify-content:space-evenly;
    flex-direction: column;
  }
  .modal-container p{
    padding-left: 40px;
    margin-right: 40px;
    width: auto;
    font-size: 24px;
  }

  .modal-content img{
    max-width: 200px;
    height: auto;
    align-self: center;
  }
  .close-button {
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: transparent;
    border: none;
    font-size: 30px;
    cursor: pointer;
  }
  
.contributers {
    display: flex;
    flex-direction: row;
    margin-left: 15px;
    margin-right: 15px;
    border-top: black solid 1px;
}
.contributers div{
    height: 100%;
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    padding: 0;
    margin: 0;
}

.modal-content svg{
    width: 200px;
    height: auto;
    align-self: center;
    color: rgb(0, 0, 0);
    background: linear-gradient(90deg, rgb(78,140,255), rgb(170, 170, 252));
    padding: 20px;
    border-radius: 20px;
}

.green{
    color: rgb(148, 69, 250);
}

.red{
    color: rgb(255, 42, 113);
}

@media only screen and (max-width: 320px){
  .modal-box {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
    width: 280px;
    height: 380px;

    display: flex;
    justify-content: center;
    flex-direction: column;
    overflow: hidden;
  }
  .modal-content p{
    font-size: 8px;
  }
  .modal-box img{
    max-height: 50px;
  }
  .modal-content svg{
    height: 40px;
    width: auto;
  }
  .close-button{
    font-size: 12px;
  }
  .modal-header p{
    font-size: 12px;
  }
  
}

