#end-of-survey{
    margin-top: 10rem;
    height: 60%;
}

#end-of-survey button{

    background-color: black;
    box-shadow: none;
    border: none;
    width: 5rem;
    height: 2rem;
    border-radius: 6px;
    color: white;
}

#end-of-survey button:hover{
    cursor: pointer;
}

@media only screen and (max-width: 320px){
    #end-of-survey{
        margin-top: 5rem;
    }
    #end-of-survey button{
        font-size: 10px;
        height: 20px;
        width: auto;
        padding-left: 15px;
        padding-right: 15px;
    }
}

@media only screen and (min-width: 321px) and (max-width: 480px){
    #end-of-survey{
        margin-top: 5rem;
    }
    #end-of-survey button{
        font-size: 10px;
        height: 20px;
        width: auto;
        padding-left: 15px;
        padding-right: 15px;
    }
    #end-of-survey p{
        font-size: 12px;
    }
}

