.nav-button-container{
    width: 100vw;
    display: flex;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0);
}

.nav-button-container svg.Inactive{
    width: 80px !important;
    height: 80px !important;
    background-color: rgb(96, 96, 96);
    border-radius: 70px;
    margin-left: 5rem;
    margin-right: 5rem;
    padding: 10px;

    transition: color 0.1s;
    transition: background-color 0.1s;
    transition: box-shadow 0.1s;
}

.nav-button-container svg.Active{
    width: 80px !important;
    height: 80px !important;
    background-color: rgb(255, 255, 255);
    border-radius: 70px;
    margin-left: 5rem;
    margin-right: 5rem;
    padding: 10px;
    /*transform: translateY(-12rem);*/
    box-shadow: 0 0 1em 0 #0d26e7;
    transition: color 0.1s;
    transition: background-color 0.1s;
    transition: box-shadow 0.1s;
}

.nav-button-container svg.Active:hover{
    background-color: rgb(0, 0, 0);
    box-shadow: 0 0 1em 0 #ffffff;
    color: white;
    cursor: pointer;
}

.footer-container {
    border-width: 10px solid var(#0d26e7);
    background-color: black;
}


.footer-text {
    border-width: 10px solid var(#0d26e7);
}


@media only screen and (max-width: 320px){
    .nav-button-container svg.Active{
        margin-left: 10px;
        margin-right: 10px;
        height: 40px;
        width: 40px;
        transform: translateY(-1rem);
    }
    .nav-button-container svg.Inactive{
        margin-left: 10px;
        margin-right: 10px;
        height: 40px;
        width: 40px;
        transform: translateY(-1rem);
    }
}


